import * as React from "react";
import { ChangeEvent, KeyboardEvent, useEffect, useState } from "react";
import s from "./ProfileStatus.module.scss";

type ProfileStatusType = {
  status: string;
  updateStatus: (status: string) => void;
  isOwner: boolean;
};

export const ProfileStatus = (props: ProfileStatusType) => {
  const [editMode, setEditMode] = useState<boolean>(false);
  const [status, setStatus] = useState<string>(props.status);
  const [error, setError] = useState<string>("");

  useEffect(() => setStatus(props.status), [props.status]);

  const activateEditMode = () => {
    props.isOwner && setEditMode(true);
  };

  const deactivateEditMode = () => {
    setEditMode(false);
    if (status.length < 20) {
      props.updateStatus(status);
      setError("");
    } else {
      setError("This string is limited to 20 characters.");
      setTimeout(() => {
        setError("");
      }, 2000);
      clearTimeout(
        setTimeout(() => {
          setError("");
        }, 2000),
      );
    }
  };

  const onStatusChange = (e: ChangeEvent<HTMLInputElement>) =>
    setStatus(e.currentTarget.value);

  const onKeyPressHandler = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter") return deactivateEditMode();
  };

  return (
    <div className={s.profileStatus}>
      {!editMode ? (
        <>
          <span className={s.status} onDoubleClick={activateEditMode}>
            {props.status ? props.status : "......"}{" "}
          </span>
          {props.isOwner && (
            <button onClick={activateEditMode} className={s.buttonChangeStatus}>
              <svg
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <g id="SVGRepo_bgCarrier" strokeWidth="0"></g>
                <g
                  id="SVGRepo_tracerCarrier"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                ></g>
                <g id="SVGRepo_iconCarrier">
                  <path
                    d="M11 4H7.2C6.0799 4 5.51984 4 5.09202 4.21799C4.71569 4.40974 4.40973 4.7157 4.21799 5.09202C4 5.51985 4 6.0799 4 7.2V16.8C4 17.9201 4 18.4802 4.21799 18.908C4.40973 19.2843 4.71569 19.5903 5.09202 19.782C5.51984 20 6.0799 20 7.2 20H16.8C17.9201 20 18.4802 20 18.908 19.782C19.2843 19.5903 19.5903 19.2843 19.782 18.908C20 18.4802 20 17.9201 20 16.8V12.5M15.5 5.5L18.3284 8.32843M10.7627 10.2373L17.411 3.58902C18.192 2.80797 19.4584 2.80797 20.2394 3.58902C21.0205 4.37007 21.0205 5.6364 20.2394 6.41745L13.3774 13.2794C12.6158 14.0411 12.235 14.4219 11.8012 14.7247C11.4162 14.9936 11.0009 15.2162 10.564 15.3882C10.0717 15.582 9.54378 15.6885 8.48793 15.9016L8 16L8.04745 15.6678C8.21536 14.4925 8.29932 13.9048 8.49029 13.3561C8.65975 12.8692 8.89125 12.4063 9.17906 11.9786C9.50341 11.4966 9.92319 11.0768 10.7627 10.2373Z"
                    stroke="#000000"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  ></path>
                </g>
              </svg>
            </button>
          )}
        </>
      ) : (
        <div className={s.statusChangeBlock}>
          <input
            className={s.inputChangeStatus}
            type="text"
            onChange={onStatusChange}
            onBlur={deactivateEditMode}
            autoFocus={true}
            onKeyPress={onKeyPressHandler}
            value={status}
          />
          <button className={s.buttonSaveStatus} onClick={deactivateEditMode}>
            Save
          </button>
        </div>
      )}
      <p className={s.error}>{error}</p>
    </div>
  );
};
